.themeSwither{position:relative;width:3rem;height:calc(1.5rem + 2px);cursor:pointer;margin:.2rem}.themeSwither::before{content:"";position:absolute;z-index:1;width:3rem;height:calc(1.5rem + 2px);border-radius:.5rem;border:var(--border-second);background-color:var(--theme-base)}.themeSwither:after{content:"☼";color:var(--theme-base);text-align:center;vertical-align:middle;line-height:1.5rem;position:absolute;z-index:2;width:1.5rem;height:1.5rem;left:1px;top:1px;right:auto;background-image:var(--theme-prime);background-blend-mode:var(--background-blend-mode);box-shadow:0 0 30px rgba(0,0,0,.055);border-radius:.5rem;transition:all .2s ease-in-out;border:var(--border-prime)}.themeSwither:checked:after{transform:translateX(calc(1.5rem - 2px));content:"☽"}@media screen and (max-width: 550px){.themeSwither{width:calc(1.5rem - 2px);height:calc(1.5rem - 2px)}.themeSwither::before{width:calc(1.5rem + 2px);top:-2px;left:-2px}.themeSwither:after{top:-1px;left:-1px}.themeSwither:checked:after{transform:none;content:"☽"}}